import React from 'react';
import './vehicle-list.css';
import { Button, Card, CardContent, List, ListItem, Typography } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { AddCircleOutline } from '@material-ui/icons';
import { vehicleList } from '../../mock/vehicles';

const VehicleList = () => {
  return (
    <div className="vehicle-list">
      <div>
        <Card style={{ width: '200px', textAlign: 'center' }}>
          <CardContent>
            <Typography>Список транспорта</Typography>
          </CardContent>
        </Card>
      </div>

      <div style={{ margin: '20px 0', width: '90%' }}>
        <Button
          color="primary"
          variant="contained"
          href="/add-vehicle"
          endIcon={<AddCircleOutline />}
        >
          Добавить транспорт
        </Button>
      </div>

      {vehicleList.map(
        ({ id, manufacturer, model, year, color, car_license_plate, organization }) => (
          <Link key={id} to={`/vehicle/${id}`} className="employee-list-link">
            <Card className="employee-list-item">
              <List>
                <ListItem>Марка: {manufacturer}</ListItem>
                <ListItem>Модель: {model}</ListItem>
                <ListItem>Имя: {year}</ListItem>
                <ListItem>Отчество: {color}</ListItem>
                <ListItem>День рождения: {car_license_plate}</ListItem>
                <ListItem>Компания {organization?.name}</ListItem>
              </List>
            </Card>
          </Link>
        ),
      )}
    </div>
  );
};
export default VehicleList;
